
body{
    font-family: "Montserrat", arial, sans-serif;
}

h2{
    font-size: 18px;
    padding: 10px 20px;
    color: #43BEB6;
}

#mario-chat{
    max-width: 600px;
    margin: 30px auto;
    border: 1px solid #9FD4E1;
    -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
            box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
    border-radius: 2px;
}

#chat-window{
    height: 400px;
    overflow: auto;
    background: #f9f9f9;
}

#output p{
    padding: 14px 0px;
    margin: 0 20px;
    border-bottom: 1px solid #9FD4E1;
    color: #555;
}

#feedback p{
    color: #aaa;
    padding: 14px 0px;
    margin: 0 20px;
}

#output strong{
    color: #43BEB6;
}

label{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    padding: 10px 20px;
}

input{
    padding: 10px 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #eee;
    border: 0;
    display: block;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid #9FD4E1;
    font-family: "Montserrat", arial, sans-serif;
    font-size: 16px;
}

#handle{
  border-top: 1px solid #9FD4E1;
}

button{
    background: #9FD4E1;
    color: #fff;
    font-size: 18px;
    border: 0;
    padding: 12px 0;
    width: 100%;
    border-radius: 0 0 2px 2px;
}
#send:hover {
  -webkit-transition: background-color 0.5s ease;
  -o-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease;
  background-color: #43BEB6;
}
button:focus {
  outline:0;
}
input:focus {
  outline:0;
}



